import {next} from "lodash/seq";

require('./scss/styles.scss');
import Swiper from 'swiper';
import {
    Thumbs,
    Grid,
    Navigation,
    Pagination,
    Autoplay,
    FreeMode,
    EffectCards,
    EffectFade,
    Controller
} from 'swiper/modules';
import {ResponsiveSlider} from "./js/ResponsiveSlider";

// import 'choices.js/public/assets/styles/choices.css';

// Импортируем Choices.js
import Choices from 'choices.js';

Swiper.use([Thumbs, Navigation, Pagination, Autoplay, FreeMode, EffectCards, EffectFade, Controller]);

// Импортируем Air Datepicker
import AirDatepicker from 'air-datepicker';

document.addEventListener("DOMContentLoaded", (event) => {

        // Инициализация Choices.js
        const choices = new Choices('#last-select', {
            searchEnabled: false, // Отключаем поиск, если он не нужен
        });


// Функция для проверки доступности даты
        function isDateBlocked(date) {
            const today = new Date();

            // Убираем время из today
            today.setHours(0, 0, 0, 0);

            const blockedDays = [25, 26, 27, 28, 29, 30]; // Блокируемые дни (с 25 по 30)
            const specificBlockedDates = [
                new Date(2024, 10, 22) // Блокируемая дата: 22 ноября 2024 года
            ];

            // 1. Блокируем даты до сегодняшней
            if (date < today) return true;

            // 2. Блокируем выходные
            if (date.getDay() === 0 || date.getDay() === 6) return true;

            // 3. Блокируем даты с 25 по 30 каждого месяца
            if (blockedDays.includes(date.getDate())) return true;

            // 4. Блокируем 22 ноября 2024 года
            for (let blockedDate of specificBlockedDates) {
                if (
                    date.getDate() === blockedDate.getDate() &&
                    date.getMonth() === blockedDate.getMonth() &&
                    date.getFullYear() === blockedDate.getFullYear()
                ) {
                    return true;
                }
            }

            return false; // Дата доступна
        }

// Функция для нахождения первой доступной даты
        function getFirstAvailableDate() {
            const today = new Date();
            for (let i = 0; i < 30; i++) { // Проверяем следующие 30 дней
                const checkDate = new Date(today);
                checkDate.setDate(today.getDate() + i);

                if (!isDateBlocked(checkDate)) {
                    return checkDate; // Возвращаем первую доступную дату
                }
            }
            return null; // Если нет доступных дат
        }

// Инициализация Air Datepicker
        const firstAvailableDate = getFirstAvailableDate();

        let datepicker;
        datepicker = new AirDatepicker('#calendar', {
            inline: true, // Показываем календарь как блок
            selectedDates: firstAvailableDate ? [firstAvailableDate] : [], // Устанавливаем первую доступную дату
            onRenderCell({date, cellType}) {
                if (isDateBlocked(date)) {
                    return {disabled: true};
                }
            },

        });


        const welcomeSlider = document.querySelector('.welcome-slider__swiper');
        if (welcomeSlider) {
            const navigation = document.querySelector('.welcome-slider__navigation');
            const nextButton = document.querySelector('.welcome-slider__next');
            const prevButton = document.querySelector('.welcome-slider__prev');

            const slider = new Swiper(".welcome-slider .swiper", {
                slidesPerView: 1,
                effect: "fade",
                loop: true,
                navigation: {
                    nextEl: '.welcome-slider__next',
                    prevEl: '.welcome-slider__prev',
                },
                pagination: {
                    el: '.welcome-slider__pagination',
                    type: 'fraction'
                },
                on: {
                    beforeSlideChangeStart: function (swiper) {
                        if (window.innerWidth >= 576) {
                            handleSlideAnimation.call(this, swiper);
                        }
                    },
                    touchMove: function (swiper) {
                        stopCustomAutoPlay();
                    }
                },
            });

            // Функция для анимации слайдов
            function handleSlideAnimation(swiper) {
                const prevSlide = this.slides[this.previousIndex];
                prevSlide.classList.add('still-visible');
                this.allowSlideNext = false;
                this.allowSlidePrev = false;
                navigation.classList.add('circle-animation');

                setTimeout(() => {
                    prevSlide.classList.remove('still-visible');
                    const currentSlide = this.slides[this.activeIndex];
                    currentSlide.classList.add('animate-back');

                    setTimeout(() => {
                        currentSlide.classList.remove('animate-back');
                        navigation.classList.remove('circle-animation');
                        this.allowSlideNext = true;
                        this.allowSlidePrev = true;
                    }, 800);
                }, 600);
            }

            let isManualClick = false; // Флаг для определения ручного клика
            function stopCustomAutoPlay() {
                clearInterval(customAutoPlay);
                customAutoPlay = null;
            }

            var customAutoPlay = setInterval(function () {
                nextButton.click();
            }, 2500);

            // Навешиваем логику на кнопки навигации
            nextButton.addEventListener('click', function (e) {
                if (isManualClick) {
                    stopCustomAutoPlay(); // Останавливаем автопрокрутку
                }
                if (!slider.allowSlideNext) return;
                handleSlideAnimation.call(slider, slider);
            });

            // Устанавливаем флаг при ручном клике
            nextButton.addEventListener('mousedown', function () {
                isManualClick = true; // Устанавливаем флаг ручного клика
            });

            prevButton.addEventListener('mousedown', function () {
                isManualClick = true; // Устанавливаем флаг ручного клика
            });


            prevButton.addEventListener('click', function (e) {
                if (isManualClick) {
                    stopCustomAutoPlay(); // Останавливаем автопрокрутку
                }
                if (!slider.allowSlidePrev) return;
                handleSlideAnimation.call(slider, slider);
            });

        }


        new ResponsiveSlider('.interiors').initSwipers({
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesPerView: "auto",
            spaceBetween: 20,
            navigation: {
                nextEl: '.interiors__prev',
                prevEl: '.interiors__next',
            },
            pagination: {
                el: '.interiors__pagination',
                type: 'fraction'
            },
            breakpoints: {
                601: {
                    spaceBetween: 24,
                },
                768: {
                    spaceBetween: 32,
                },
                992: {
                    spaceBetween: 36,
                    slidesOffsetBefore: 0,
                },
                1440: {
                    spaceBetween: 48,
                    slidesOffsetBefore: 0,
                },
            }
        }, true);


        new Swiper(".video-slider__slider.swiper", {
            slidesPerView: 1,
            navigation: {
                nextEl: '.video-slider__next',
                prevEl: '.video-slider__prev',
            },
            pagination: {
                el: '.video-slider__pagination', // Это можно оставить, если нужен индикатор
                type: 'custom', // Устанавливаем кастомную пагинацию
                renderCustom: function (swiper, current, total) {
                    document.querySelector('.video-slider__current-num').textContent = current <= 9 ? '0' + current : current;
                    document.querySelector('.video-slider__total-num').textContent = total <= 9 ? '0' + total : total;
                }
            },
        });


        let swiperInstances = []; // Массив для хранения экземпляров Swiper
        const initTabSliders = () => {
            const screenWidth = window.innerWidth;

            const swiperElements = document.querySelectorAll('.accordeon-block__slider');
            if (screenWidth < 992) {
                swiperElements.forEach((element, index) => {

                    if (!swiperInstances[index]) {
                        swiperInstances[index] = new Swiper(element, {
                            slidesPerView: "auto",
                            spaceBetween: 20,
                        });
                    }
                });
            } else {
                // Если экран больше 992px, уничтожаем все Swiper'ы
                swiperInstances.forEach((swiper, index) => {
                    if (swiper) {
                        swiper.destroy(true, true); // Уничтожаем Swiper
                        swiperInstances[index] = null; // Очищаем экземпляр
                    }
                });
            }
        };

        const tabSliders = document.querySelectorAll('.accordeon-block__slider');
        if (tabSliders.length > 0) {
            initTabSliders();
            window.addEventListener('resize', function () {
                initTabSliders();
            });
        }

        function scrollToElementWithHeaderOffset(element, additionalHeight) {
            const headerHeight = document.querySelector('header').offsetHeight; // Высота хедера
            const elementPosition = element.getBoundingClientRect().top; // Позиция элемента относительно верхней границы окна
            const offsetPosition = elementPosition - headerHeight - additionalHeight; // Позиция с учетом высоты хедера

            // Прокручиваем страницу на нужное расстояние
            window.scrollBy({
                top: offsetPosition,
                behavior: 'smooth'
            });
        }

        const tabsBlock = document.querySelectorAll('[data-tabs]');
        if (tabsBlock.length) {
            tabsBlock.forEach(tabsBlock => {
                const btns = tabsBlock.querySelectorAll('[data-tab]');
                const blocks = tabsBlock.querySelectorAll('[data-tab-block]');
                for (let btn of btns) {
                    btn.addEventListener('click', () => {
                        const id = btn.getAttribute('data-tab');
                        for (let block of blocks) {
                            if (block.getAttribute('data-tab-block') === id) {
                                block.setAttribute('data-active', '');
                                scrollToElementWithHeaderOffset(block, 150);
                            } else {
                                block.removeAttribute('data-active');
                            }
                        }
                        btn.setAttribute('data-active', '');
                        for (let otherBtn of btns) {
                            if (otherBtn.getAttribute('data-tab') !== id) {
                                otherBtn.removeAttribute('data-active');
                            }
                        }
                    });
                }
            });
        }

    }
);